import React, { useEffect, useState } from "react";
import { Skeleton, Table, Tag, Button } from "antd";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { getStroyMap } from "../../../action/project";
import useTitle from "../../util/useTitle";
const Index = props => {
  useTitle("project story map");

  const dispatch = useDispatch();
  const { loading, activiteis, sprints } = useSelector(
    state => state.storyMap,
    shallowEqual
  );

  const { user } = useSelector(state => state.auth, shallowEqual);
  const columnWidth = 150;

  const [filtering, setFiltering] = useState(false);

  const convertDataToColumn = activiteis =>
    activiteis.map((act, index) => ({
      title: act.activityName,
      dataIndex: "stories",
      key: index,
      width: columnWidth,
      render: stories => (
        <span>
          {filtering
            ? stories
                .filter(
                  story =>
                    story.activityId === act.activityId &&
                    story.member.find(item => item.userId === user.userId)
                )
                .map(filteredStory => (
                  <Tag
                    color="blue"
                    style={{ margin: "4px" }}
                    key={filteredStory.storyId}
                  >
                    {`${filteredStory.storyName} - ${filteredStory.storyPoint}`}
                  </Tag>
                ))
            : stories
                .filter(story => story.activityId === act.activityId)
                .map(filteredStory => (
                  <Tag
                    color="blue"
                    style={{ margin: "4px" }}
                    key={filteredStory.storyId}
                  >
                    {`${filteredStory.storyName} - ${filteredStory.storyPoint}`}
                  </Tag>
                ))}
        </span>
      )
    }));

  const getSprintStoriesPoint = activiteis => {
    return {
      title: "Story Points",
      dataIndex: "stories",
      key: "storyPoints",
      width: columnWidth,
      render: stories => {
        let total = 0;
        return (
          <div>
            {filtering
              ? stories
                  .filter(
                    story =>
                      true &&
                      story.member.find(item => item.userId === user.userId)
                  )
                  .map(story => (
                    <Tag
                      color="blue"
                      style={{ margin: "4px" }}
                      key={story.storyId}
                    >
                      {story.storyPoint}
                    </Tag>
                  ))
              : stories.map(story => (
                  <>
                    <Tag
                      color="blue"
                      style={{ margin: "4px" }}
                      key={story.storyId}
                    >
                      {story.storyPoint}
                    </Tag>
                  </>
                ))}
            <span>
              {filtering
                ? stories
                    .filter(
                      story =>
                        true &&
                        story.member.find(item => item.userId === user.userId)
                    )
                    .forEach(
                      story => (total = total + parseInt(story.storyPoint))
                    )
                : stories.forEach(
                    story => (total = total + parseInt(story.storyPoint))
                  )}
              <Tag>{total}</Tag>
            </span>
          </div>
        );
      }
    };
  };

  useEffect(() => {
    dispatch(getStroyMap(props.match.params.projectId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.match.params.projectId]);

  return (
    <>
      {loading || activiteis.length === 0 ? (
        <>
          <div>
            <div
              style={{
                height: "120px",
                overflow: "hidden",
                width: "100%",
                margin: "auto 0"
              }}
            >
              <Skeleton active />
            </div>
          </div>
        </>
      ) : (
        <div>
          <div
            style={{
              margin: "10px  auto",
              display: "flex",
              justifyContent: "center"
            }}
          >
            <Button
              type={!filtering ? "primary" : "normal"}
              onClick={() => setFiltering(false)}
            >
              {" "}
              all work
            </Button>
            <span style={{ margin: "10px" }}></span>
            <Button
              type={filtering ? "primary" : "normal"}
              onClick={() => setFiltering(true)}
            >
              {" "}
              my work
            </Button>
          </div>

          <Table
            title={() => <b>{filtering ? "my works" : "all works"}</b>}
            rowKey={record => record.sprintId}
            loading={loading}
            bordered={true}
            scroll={{
              x: activiteis.length * columnWidth + columnWidth + columnWidth
            }}
            columns={[
              {
                title: "sprint name",
                dataIndex: "sprintName",
                key: "312",
                width: columnWidth
                // fixed: 'left',
              },
              ...convertDataToColumn(activiteis),
              getSprintStoriesPoint(activiteis)
            ]}
            dataSource={sprints}
            pagination={{ position: "top" }}
          />
        </div>
      )}
    </>
  );
};

export default Index;
